.primary-button {
    display: flex;
    height: 40px;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    outline: none;
    border: none;
    cursor: pointer;
  
    background-color: #4094ef;
    color: #fff;
    text-align: center;
    font-family: Roboto;
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: 24px; /* 171.429% */
  }
  
  .button-text {
    color: #fff;
    text-align: center;
    font-family: Roboto;
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: 24px; /* 171.429% */
  }
  .primary-button:hover {
    box-shadow: 0px 4px 16px 0px #bfbfbf;
  }
  
  .loader-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
    width: 100%;
  }